.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
}
//==========================================
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}
//==========================================
@media screen and (min-width: 1440px) {
  .container {
    width: 1060px;
  }
}
